/* Global styles */
body {
  font-family: Arial, sans-serif;
  background-color: rgb(255, 183, 50);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Login page container */
.box {
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  padding: 30px;
  max-width: 400px;
  width: 180%;
 
}

/* Login page title */
h2 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 20px;
  color: #007bff;
}

/* Form styles */
.form-control {
  border: 2px solid #f9e34c;
  border-radius: 8px;
  padding: 14px;
  width: 100%;
  font-size: 16px;
  transition: border-color 0.3s;
}

.form-control:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

/* Button styles */
.d-grid {
  margin-top: 20px;
}

.btn-primary {
  background-color: #007bff;
  border: none;
  font-size: 18px;
  padding: 14px;
  width: 100%;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-primary:hover {
  background-color: #0056b3;
}

/* Google button styles (assuming it's a third-party component) */
.g-btn {
  width: 100%;
  margin-top: 20px;
}

/* Link styles */
.box.text-center {
  margin-top: 20px;
  text-align: center;
  color: #060101;
  font-size: 16px;
}

.box.text-center a {
  color: #007bff;
  text-decoration: none;
}

.box.text-center a:hover {
  text-decoration: underline;
}
